import React from 'react';
import { ModalWrapper } from 'carbon-components-react';
class Modal extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
        pic: true, 
        label: this.props.label,
        filename: this.props.file,
        url: 'http://doc-access-service-document-access.cp4i-errortest-dal10-c3-f2c6cdc6801be85fd188b09d006f13e3-0000.us-south.containers.appdomain.cloud/getFile/'+this.props.file,
    };
  }
    
  render() {
    return (
          <div>
              <ModalWrapper
              buttonTriggerText={this.state.label}
              modalHeading={this.state.filename}
              modalLabel="Document Copy"
              passiveModal
              >
                { this.state.pic ? <img style={{ width: "100%" }} src={this.state.url} alt=""/> : "" }
            </ModalWrapper>
          </div>
        )
  }
}

export default Modal;