import React from 'react';
import { Header, HeaderName } from 'carbon-components-react/lib/components/UIShell';
import { Tile } from 'carbon-components-react';
import { DataTable, TableContainer, Table, TableHead, TableRow, TableHeader, TableBody, TableCell  } from 'carbon-components-react';
import OverflowItem from './OverflowItem';
// import DataTableButton from './DataTableButton';
import Modal from './Modal';

class SavingsOfficialDashboard extends React.Component {

    name="Savings Branch Official";

    constructor(props) {
      super(props);
      this.state = {
          rowData: [],
          isLoaded: false,
          errMessage: null,
          userInfo: {}
      };
    }

    componentDidMount = async () => {
      // fetch("http://approval-service-approval-flow.cpi-ocp47-cluster-f2c6cdc6801be85fd188b09d006f13e3-0000.us-east.containers.appdomain.cloud/approval-flow/account/savings/pendinglist", { method: "get" })
      await fetch("http://approval-service-approval-proj.cp4i-errortest-dal10-c3-f2c6cdc6801be85fd188b09d006f13e3-0000.us-south.containers.appdomain.cloud/approval-flow/account/savings/pendinglist", { method: "get" })
      // await fetch("http://localhost:9080/approval-flow/account/savings/pendinglist", { method: "get" })
        .then(res => {
          if (res.status >= 300) {
            this.setState({
              errMessage: 'Internal server error occurred. Check server logs.'
            });
          };
          return res.json();
        })
        .then(
            (result) => {
              let arr=[];
              result.forEach ((result) => {
                // alert(JSON.stringify(result));
                let nid_filename = result.user_id + '-nationalid.png';
                let tid_filename = result.user_id + '-taxid.png';

                let user_info = {
                  user_id: result.user_id,
                  first_name: result.first_name,
                  last_name: result.last_name,
                  mobile_no: result.mobile_no,
                  email_id: result.email_id
                };
                this.setState ( { userInfo: user_info } );

                let tempObj = {
                  id: result.user_id,
                  first_name: result.first_name,
                  last_name: result.last_name,
                  mobile_no: result.mobile_no,
                  email_id: result.email_id,
                  address: result.address,
                  // national_id: <DataTableButton label='Get ID' file={nid_filename} />,
                  national_id: <Modal label='Get NationalID' file={nid_filename}/>,
                  tax_id: <Modal label='Get TaxID' file={tid_filename}/>,
                  // tax_id: <DataTableButton label='Get ID' file={tid_filename}/>,
                  apply_date: result.apply_date,
                  overflow: <OverflowItem account='savings' userInfo={this.state.userInfo}/>
                }
                arr.push(tempObj);
              });
              this.setState( {isLoaded: true, rowData: arr} );
        },
        (error) => {
          alert("Server Error: ", JSON.stringify(this.state.errMessage));
          }
        )
      }
    
    headerData = [
      {
        key: 'first_name',
        header: 'First Name',
      },
      {
        key: 'last_name',
        header: 'Last Name',
      },
      {
        key: 'mobile_no',
        header: 'Mobile',
      },
      {
        key: 'email_id',
        header: 'Email ID',
      },
      {
        key: 'national_id',
        header: 'National ID',
      },
      {
        key: 'tax_id',
        header: 'Tax ID',
      },
      {
        key: 'address',
        header: 'Address',
      },
      {
        key: 'apply_date',
        header: 'Applied_on',
      },
      {
        key: 'overflow',
        header: '',
      },
    ];

    render(){
 
      if (!this.state.isLoaded) {
        return (
          <div>
            <div className="container">
              <Header aria-label="IBM Platform Name" >
                <HeaderName href="#" prefix="" style={{ marginLeft: "43%", fontSize: "25px"}}>
                  Sample Bank Demo
                </HeaderName>
              </Header>
            </div>

            <div style={{marginTop: "5%", marginLeft: "5%"}}> Loading ... </div>
          </div>
        );
      } else {
        return (
          <div>
            <div className="container">
              <Header aria-label="IBM Platform Name" >
                <HeaderName href="#" prefix="" style={{ marginLeft: "43%", fontSize: "25px"}}>
                  Sample Bank Demo
                </HeaderName>
              </Header>
            </div>

            <div style={{marginTop: "5%", marginLeft: "5%", marginRight: "5%", textAlign: "center", display:"flex", border: "2px solid white", justifyContent: "center"}}>
            {/* border: "2px solid red", */}

                <div style={{ marginLeft: "2rem", marginBottom: '20rem', marginTop: '1rem', marginRight: "2rem", border: "2px solid white", width: "80%", height: "70px", textAlign:"left", position: "relative"}}> 
                  <Tile>
                    <h4>Welcome {this.name} !</h4>
                  </Tile> 
                </div>
              
                <div style={{ marginLeft: "2rem", marginBottom: '2rem', marginTop: '8rem', marginRight: "2rem", border: "1px solid white", width: "72%", textAlign:"left", position: "absolute"}}> 
                
                  <DataTable rows={this.state.rowData} headers={this.headerData}>
                    {({ rows, headers, getHeaderProps, getTableProps }) => (
                      <TableContainer title="List of Pending Savings Accounts">
                        <Table {...getTableProps()}>
                          <TableHead>
                            <TableRow>
                              {headers.map((header) => (
                                <TableHeader {...getHeaderProps({ header })}>
                                  {header.header}
                                </TableHeader>
                              ))}
                            </TableRow>
                          </TableHead>
                          <TableBody>
                            {rows.map((row) => (
                              <TableRow key={row.id}>
                                {row.cells.map((cell) => (
                                  <TableCell key={cell.id}>{cell.value}</TableCell>
                                ))}
                              </TableRow>
                            ))}
                          </TableBody>
                        </Table>
                      </TableContainer>
                    )}
                  </DataTable>
                </div>
            </div>
          </div>
        )
      }
    }
}

export default SavingsOfficialDashboard;


