import React from 'react';
import { Tile } from 'carbon-components-react';

class SavingsAccount extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      AccountNumber: this.props.accountDetails.accountNumber,
      AccountBalance: this.props.accountDetails.accountBalance,
    };
  }
    
    render(){

      return (
      <div>
        <h3> Savings Account details</h3>
            <br></br>
            <Tile>
               <p>Account Number : {this.state.AccountNumber}</p><br></br>
               <p>Account Balance : {this.state.AccountBalance}</p><br></br>
            </Tile> 
      </div>
      )
    }
}

export default SavingsAccount;


