import React from 'react';
import { Tile } from 'carbon-components-react';

class LoanAccount extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      AccountNumber: this.props.accountDetails.accountNumber,
      AccountBalance: this.props.accountDetails.accountBalance,
      RateOfInterest: this.props.accountDetails.rateOfInterest,
      LoanType: this.props.accountDetails.loanType
    };
  }
    
    render(){
   
      return (
      <div>
        <h3> Loan Account details</h3>
        <br></br>
        <Tile>
          <p>Loan Account Number : {this.state.AccountNumber}</p><br></br>
          <p>Loan Amount : {this.state.AccountBalance}</p><br></br>
          <p>Type of Loan : {this.state.LoanType}</p><br></br>
          <p>Rate of Interest : {this.state.RateOfInterest}</p> <br></br>
        </Tile>
      </div>
      )
    }
}

export default LoanAccount;


