import React from 'react';
import { Header, HeaderName } from 'carbon-components-react/lib/components/UIShell';
import { Tile, Tabs, Tab } from 'carbon-components-react';
import LoanAccount from './LoanAccount';
import SavingsAccount from './SavingsAccount';
import Message from './Message';

class UserDashboard extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        isLoaded: false,
        isLoanAccount: false,
        isSavingsAccount: true,
        userDetails: {},
        savingsAccountDetails: {},
        loanAccountDetails: {}
      };
    }
    
    componentDidMount = async () => {
      let savingsAccUrl="http://savings-acc-svc-savings-project.cp4i-errortest-dal10-c3-f2c6cdc6801be85fd188b09d006f13e3-0000.us-south.containers.appdomain.cloud/locker/savings";
      let url = savingsAccUrl + '?userid=UL123';
      await fetch(url, 
      { method: "get" ,
        headers: { 'Authorization' : 'Bearer sfdfdfg'}
      })
        .then(res => {
          // alert(res.status);
          return res.json();
        })
        .then(
            (result) => {
              // alert(JSON.stringify(result));
              // alert(result.email_id);
              let userObj = {
                userid : result.user_id,
                username: result.first_name + ' ' + result.last_name,
                mobile_no: result.mobile_no,
                email_id: result.email_id
              }
              this.setState( { userDetails: userObj });

              let savingsAccountInfo = {
                accountNumber: result.savings_account_no,
                accountBalance: result.accoun_balance
              }
              this.setState( { savingsAccountDetails: savingsAccountInfo });

              if (result.has_loan === 'no') {
                this.setState( { isLoanAccount: false });
                this.setState( { isLoaded: true });
              } else {
                this.setState( { isLoanAccount: true });
                let loanAccUrl="http://loan-acc-svc-loan-project.cp4i-errortest-dal10-c3-f2c6cdc6801be85fd188b09d006f13e3-0000.us-south.containers.appdomain.cloud/locker/loan";
                let url = loanAccUrl + '?userid=UL123';
                fetch(url, 
                { method: "get" ,
                  headers: { 'Authorization' : 'Bearer sfdfdfg'}
                })
                .then( res => { return res.json(); })
                .then(
                  (data) => {
                    // alert(JSON.stringify(data));
                    let loanAccountInfo = {
                      accountNumber: data.loan_account_no,
                      accountBalance: data.loan_amount,
                      rateOfInterest: data.rate_of_interest,
                      loanType: data.loan_type
                    }
                    this.setState( { loanAccountDetails: loanAccountInfo });
                    this.setState( { isLoaded: true });
                  },
                  (error) => {
                    alert("Internal Serval Error while fetching Loan Account Details");
                  }
                )
              }
              // alert(JSON.stringify(this.state));
              // this.setState( { isLoaded: true });
            },
            (error) => {
              alert("Internal Server Error: ", JSON.stringify(error));
            }
          )
    }

    render(){
   
      let accountDetails, loanDetails;

      if (this.state.isSavingsAccount) {
        accountDetails = <SavingsAccount accountDetails={this.state.savingsAccountDetails}/>;
      }
      
      if (this.state.isLoanAccount) {
        loanDetails = <LoanAccount accountDetails={this.state.loanAccountDetails}/>;  
      } else {
        loanDetails = <Message userInfo={this.state.userDetails}/>;
      }

      if (!this.state.isLoaded) {
        return (
          <div>
            <div className="container">
              <Header aria-label="IBM Platform Name" >
                <HeaderName href="#" prefix="" style={{ marginLeft: "43%", fontSize: "25px"}}>
                  Sample Bank Demo
                </HeaderName>
              </Header>
            </div>

            <div style={{marginTop: "5%", marginLeft: "5%"}}> Loading ... </div>
          </div>
        );
      } else {
        return (
        <div>
          <div className="container">
            <Header aria-label="IBM Platform Name" >
              <HeaderName href="#" prefix="" style={{ marginLeft: "43%", fontSize: "25px"}}>
                Sample Bank Demo
              </HeaderName>
            </Header>
          </div>

          <div style={{marginTop: "5%", marginLeft: "5%", marginRight: "5%", textAlign: "center", display:"flex", border: "2px solid white", justifyContent: "center"}}>
          {/* border: "2px solid red", */}

              <div style={{ marginLeft: "2rem", marginBottom: '20rem', marginTop: '2rem', marginRight: "2rem", border: "2px solid white", width: "80%", height: "70px", textAlign:"left", position: "relative"}}> 
                <Tile>
                  <h4>Welcome {this.state.userDetails.username} !</h4>
                </Tile> 
              </div>
            
              <div style={{ marginLeft: "2rem", marginBottom: '2rem', marginTop: '10rem', marginRight: "2rem", border: "1px solid white", width: "72%", textAlign:"left", position: "absolute"}}> 
              
                <Tabs scrollIntoView={false} type="container">
                  <Tab
                    href="#"
                    id="tab-1"
                    label="Savings Account"
                  >
                    <div className="some-content" style = {{ border: "2px solid white"}}>
                      {accountDetails}
                    </div>
                  </Tab>

                  <Tab
                    href="#"
                    id="tab-2"
                    label="Loan Account"
                  >
                    <div className="some-content">
                      {loanDetails}
                    </div>
                  </Tab>
                </Tabs>
              </div>
          </div>
        </div>
        )
      }
    }
}

export default UserDashboard;


