import React from 'react';
import { Header, HeaderName } from 'carbon-components-react/lib/components/UIShell';
import { Form, TextInput, Button, FileUploader, FormGroup, RadioButtonGroup, RadioButton } from 'carbon-components-react';
import { Link } from 'react-router-dom';
import UserDashboard from './UserDashboard';


class RequestLoanAccount extends React.Component {

    constructor() {
      super();
      this.state = {
        userid: null,
        username: null,
        mobile_no: 0,
        email_id: null,
        incomeProof: null,
        loan_type: null,
        isLoadNew: false
      }
    }

    componentDidMount() {
      // save it to the state
      let { userDetails } = this.props.location.state;
      this.setState({ userid: userDetails.userid });
      this.setState({ username: userDetails.username });
      this.setState({ mobile_no: userDetails.mobile_no });
      this.setState({ email_id: userDetails.email_id });
    }

    onFileChange = event => {
      // Update the state
      this.setState({ incomeProof: event.target.files[0] });
    }

    radioButtonChange = value => {
      this.setState({ loan_type: value });
    }

    submitLoanAccountRequest = () => {

      const data = new FormData();
      
      data.append('userid', this.state.userid);
      data.append('income', document.getElementById('yearlyincome').value);
      data.append('loan_type', this.state.loan_type);
      data.append('loan_amount', document.getElementById('loan_amount').value);
      // data.append('applydate', this.state.date);
      data.append('incomeprooffile', this.state.incomeProof);

      fetch("http://loan-acc-svc-loan-project.cp4i-errortest-dal10-c3-f2c6cdc6801be85fd188b09d006f13e3-0000.us-south.containers.appdomain.cloud/locker/loan", { 
        body: data,
        method: "post" })
        .then(
          (success) => {
            console.log(JSON.stringify(success));
            alert("Loan request has been submitted successfully");
            this.setState({ isLoadNew: true });
          },
          (error) => {
            alert ("Internal Server Error." + JSON.stringify(error));
          }
        );
      // this.setState({ isLoadNew: true });
      // alert("Loan request has been submitted successfully");
    }

    render(){
      if ( this.state.isLoadNew ) {
        return (<div> <UserDashboard /> </div> )
      } else {
        return (
        <div>
          <div className="container">
            <Header aria-label="IBM Platform Name" >
              <HeaderName href="#" prefix="" style={{ marginLeft: "45%", fontSize: "large"}}>
                Sample Bank Demo
              </HeaderName>
            </Header>
          </div>
          
          <div style={{marginTop: "4%", marginLeft: "5%", marginRight: "5%", textAlign: "center", display:"flex", justifyContent: "center"}}>
          {/* border: "2px solid red", */}
            <Form>
              <div style={{marginBottom: '2rem', marginTop: '0rem', fontSize: "large"}}> 
                <p> Apply for Loan </p> 
              </div>

              <div style={{marginBottom: '2rem', width: '500px'}}>
                <TextInput
                  id="Name"
                  invalidText="Invalid error message."
                  labelText="Name"
                  value={this.state.username}
                  disabled
                />
              </div>
              <div style={{marginBottom: '2rem', width: '500px'}}>
                <TextInput
                  id="mobilenum"
                  invalidText="Invalid error message."
                  labelText="Mobile Number"
                  value={this.state.mobile_no}
                  disabled
                />
              </div>
              <div style={{marginBottom: '2rem', width: '500px'}}>
                <TextInput
                  id="emailid"
                  invalidText="Invalid error message."
                  labelText="EmailID"
                  value={this.state.email_id}
                  disabled
                />
              </div>
              <div style={{marginBottom: '2rem', width: '500px'}}>
                <TextInput
                  id="yearlyincome"
                  invalidText="Invalid error message."
                  labelText="Yearly Income"
                  placeholder="Provide your yearly income"
                />
              </div>
              <div style={{marginBottom: '2rem', width: '500px'}}>
                <FormGroup legendText="Loan Type">
                  <RadioButtonGroup defaultSelected="default-selected"  onChange={this.radioButtonChange} legend="Group Legend"  name="radio-button-group" valueSelected="default-selected" >
                    <RadioButton id="homeloan"  name="homeloan" labelText="Home Loan"  value="home" />
                    <RadioButton id="vehicleloan" name="vehicleloan" labelText="Vehicle Loan"  value="vehicle" />
                  </RadioButtonGroup>
                </FormGroup>
              </div>
              <div style={{marginBottom: '2rem', width: '500px'}}>
                <TextInput
                  id="loan_amount"
                  invalidText="Invalid error message."
                  labelText="Loan Amount"
                  placeholder="Provide your loan amount"
                />
              </div>
              <div style={{marginBottom: '2rem', width: '500px'}} >
                <FileUploader
                  accept={[
                    '.jpg',
                    '.png'
                  ]}
                  buttonKind="tertiary"
                  size="small"
                  buttonLabel="Add files"
                  filenameStatus="edit"
                  iconDescription="Clear file"
                  labelDescription="only .png files at 500mb or less"
                  labelTitle="Upload Income Proof Document"
                  onChange={this.onFileChange}
                />
              </div>

              <Button
                kind="primary"
                tabIndex={0}
                type="submit"
                onClick={this.submitLoanAccountRequest}>
                Apply for Loan
              </Button>
              <br /><br />
              <Link to="/user-dashboard">Back</Link>
              <br /><br />
            </Form>
          </div>
        </div>
      )}
    }
}

export default RequestLoanAccount;


