import React from 'react';
import { Header, HeaderName } from 'carbon-components-react/lib/components/UIShell';
import { Tile } from 'carbon-components-react';
import { DataTable, TableContainer, Table, TableHead, TableRow, TableHeader, TableBody, TableCell  } from 'carbon-components-react';
import OverflowItem from './OverflowItem';
import Modal from './Modal';

class LoanOfficialDashboard extends React.Component {

    name="Loan Branch Official";

    constructor(props) {
      super(props);
      this.state = {
          rowData: [],
          isLoaded: false,
          userInfo: {}
      };
    }

    componentDidMount() { 
      fetch("http://approval-service-approval-proj.cp4i-errortest-dal10-c3-f2c6cdc6801be85fd188b09d006f13e3-0000.us-south.containers.appdomain.cloud/approval-flow/account/loan/pendinglist", { method: "get" })
        .then(res => res.json())
        .then(
            (result) => {
              // alert(JSON.stringify(result.length));
              let arr=[];
              result.forEach ((result) => {
                let tempObj = {};
                let nid_filename = result.user_id + '-nationalid.png';
                let tid_filename = result.user_id + '-taxid.png';
                let ip_filename = result.user_id + '-incomeproof.png';
                let user_info = {
                  "user_id": result.user_id,
                  "approver_id": "employee14",
                  "rate_of_interest": 14.0,
                  "reject_reason": "NA",
                  "status": "approve"
                };
                // alert(JSON.stringify(user_info));
                this.setState ( { userInfo: user_info } );
                tempObj = {
                  id: result.user_id,
                  name: result.first_name + ' ' + result.last_name,
                  income: result.income,
                  loan_type: result.loan_type,
                  loan_amount: result.loan_amount,
                  address: result.address,
                  national_id: <Modal label='Get NationalID' file={nid_filename}/>,
                  tax_id: <Modal label='Get TaxID' file={tid_filename}/>,
                  income_proof: <Modal label='Get Doc' file={ip_filename}/>,
                  // national_id: <DataTableButton label='Get ID' file={nid_filename} />,
                  // tax_id: <DataTableButton label='Get ID' file={tid_filename}/>,
                  // income_proof: <DataTableButton label='Get Doc' file={ip_filename}/>,
                  apply_date: result.apply_date,
                  overflow: <OverflowItem account='loan' userInfo={this.state.userInfo}/>
                }
                arr.push(tempObj);
                // alert(JSON.stringify(arr));
              });
              this.setState( {isLoaded: true, rowData: arr} );
              // alert(this.state.isLoaded);
              // alert(this.state.rowData);
        },
        (error) => {
          this.setState({
            data: error.message
          });
          alert(error.message);
        }
      )
    }
    
    headerData = [
      {
        key: 'name',
        header: 'Name',
      },
      {
        key: 'income',
        header: 'Yearly Income',
      },
      {
        key: 'loan_type',
        header: 'Type of Loan',
      },
      {
        key: 'loan_amount',
        header: 'Loan Requested',
      },
      {
        key: 'national_id',
        header: 'National ID',
      },
      {
        key: 'tax_id',
        header: 'TaxID',
      },
      {
        key: 'income_proof',
        header: 'Income Proof',
      },
      {
        key: 'address',
        header: 'Address',
      },
      {
        key: 'apply_date',
        header: 'Apply date',
      },
      {
        key: 'overflow',
        header: '',
      },
    ];
    
    render(){
   
      if (!this.state.isLoaded) {
        return (
          <div>
            <div className="container">
              <Header aria-label="IBM Platform Name" >
                <HeaderName href="#" prefix="" style={{ marginLeft: "43%", fontSize: "25px"}}>
                  Sample Bank Demo
                </HeaderName>
              </Header>
            </div>

            <div style={{marginTop: "5%", marginLeft: "5%"}}> Loading ... </div>
          </div>
        );
      } else {
        return (
          <div>
            <div className="container">
              <Header aria-label="IBM Platform Name" >
                <HeaderName href="#" prefix="" style={{ marginLeft: "43%", fontSize: "25px"}}>
                  Sample Bank Demo
                </HeaderName>
              </Header>
            </div>

            <div style={{marginTop: "5%", marginLeft: "5%", marginRight: "5%", textAlign: "center", display:"flex", border: "2px solid white", justifyContent: "center"}}>
            {/* border: "2px solid red", */}

                <div style={{ marginLeft: "2rem", marginBottom: '20rem', marginTop: '1rem', marginRight: "2rem", border: "2px solid white", width: "80%", height: "70px", textAlign:"left", position: "relative"}}> 
                  <Tile>
                    <h4>Welcome {this.name} !</h4>
                  </Tile> 
                </div>
              
                <div style={{ marginLeft: "2rem", marginBottom: '2rem', marginTop: '8rem', marginRight: "2rem", border: "1px solid white", width: "72%", textAlign:"left", position: "absolute"}}> 
                
                  <DataTable rows={this.state.rowData} headers={this.headerData}>
                    {({ rows, headers, getHeaderProps, getTableProps }) => (
                      <TableContainer title="List of Pending Loan Accounts">
                        <Table {...getTableProps()}>
                          <TableHead>
                            <TableRow>
                              {headers.map((header) => (
                                <TableHeader {...getHeaderProps({ header })}>
                                  {header.header}
                                </TableHeader>
                              ))}
                            </TableRow>
                          </TableHead>
                          <TableBody>
                            {rows.map((row) => (
                              <TableRow key={row.id}>
                                {row.cells.map((cell) => (
                                  <TableCell key={cell.id}>{cell.value}</TableCell>
                                ))}
                              </TableRow>
                            ))}
                          </TableBody>
                        </Table>
                      </TableContainer>
                    )}
                  </DataTable>
                </div>
            </div>
          </div>
        )
      }
    }
}

export default LoanOfficialDashboard;


