import React from 'react';
import { Header, HeaderName } from 'carbon-components-react/lib/components/UIShell';
import { Form, TextInput, Button, FileUploader } from 'carbon-components-react';
import { Link } from 'react-router-dom';

class RequestSavingsAccount extends React.Component {

    constructor() {
      super();
      this.state = {
        nationalID: null ,
        taxID: null 
      }
    }

    onNIDChange = event => {
      console.log(event.target.files[0]);
      // Update the state
      this.setState({ nationalID: event.target.files[0] });
    }

    onTIDChange = event => {
      console.log(event.target.files[0]);
      // Update the state
      this.setState({ taxID: event.target.files[0] });
    }

    createSavingsAccount = async () => {
      // alert("Create Account Button Clicked");
      const data = new FormData();
      
      data.append('firstname', document.getElementById('firstname').value);
      data.append('lastname', document.getElementById('lastname').value);
      data.append('userid', document.getElementById('userid').value);
      data.append('mobilenumber', document.getElementById('mobilenum').value);
      data.append('address', document.getElementById('address').value);
      // data.append('emailid', 'abc@email.com');
      data.append('emailid', document.getElementById('emailid').value);
      // data.append('applydate', this.state.date);
      data.append('nationalidfile', this.state.nationalID);
      data.append('taxidfile', this.state.taxID);
      
      await fetch("http://savings-acc-svc-savings-project.cp4i-errortest-dal10-c3-f2c6cdc6801be85fd188b09d006f13e3-0000.us-south.containers.appdomain.cloud/locker/savings", { 
        body: data,
        method: "post" })
        .then (
          (success) => {
            console.log(success);
            alert("Request of creating Savings Account is submitted successfully");
          },
          (error) => {
            alert("Internal Server Error - " + JSON.stringify(error));
          }
        );
      // alert(JSON.stringify(response));
      // alert("Request of creating Savings Account is submitted successfully");
    }
    
    render(){
   
      return (
      <div>
        <div className="container">
          <Header aria-label="IBM Platform Name" >
            <HeaderName href="#" prefix="" style={{ marginLeft: "43%", fontSize: "25px"}}>
              Sample Bank Demo
            </HeaderName>
          </Header>
        </div>

        <div style={{marginTop: "4%", marginLeft: "5%", marginRight: "5%", textAlign: "center", display:"flex", justifyContent: "center"}}>
        {/* border: "2px solid red", */}
          <Form>
            <div style={{marginBottom: '2rem', marginTop: '0rem', fontSize: "large"}}> 
              <p> Request Savings Account </p> 
            </div>

            <div style={{marginBottom: '2rem', width: '500px'}}>
              <TextInput
                id="firstname"
                invalidText="Invalid error message."
                labelText="Firstname"
                placeholder="Provide your firstname"
                required
              />
            </div>
            <div style={{marginBottom: '2rem', width: '500px'}}>
              <TextInput
                id="lastname"
                invalidText="Invalid error message."
                labelText="Lastname"
                placeholder="Provide your lastname"
                required
              />
            </div>
            <div style={{marginBottom: '2rem', width: '500px'}}>
              <TextInput
                id="userid"
                invalidText="Invalid error message."
                labelText="UserID"
                placeholder="Provide your user ID"
                required
              />
            </div>
            <div style={{marginBottom: '2rem', width: '500px'}}>
              <TextInput
                id="mobilenum"
                invalidText="Invalid error message."
                labelText="Mobile Number"
                placeholder="Provide your mobile number"
                required
              />
            </div>
            <div style={{marginBottom: '2rem', width: '500px'}}>
              <TextInput
                id="address"
                invalidText="Invalid error message."
                labelText="Address"
                placeholder="Provide your address"
                required
              />
            </div>
            <div style={{marginBottom: '2rem', width: '500px'}}>
              <TextInput
                id="emailid"
                invalidText="Invalid error message."
                labelText="Email ID"
                placeholder="Provide your email ID"
                required
              />
            </div>
            <div style={{marginBottom: '2rem', width: '500px', display:'none'}}>
              <TextInput
                id="dummy"
                invalidText="Invalid error message."
                labelText="dummy"
                placeholder="Provide your text"
                required
              />
            </div>
            <div style={{marginBottom: '1rem', width: '500px'}} >
              <FileUploader
                accept={[
                  '.jpg',
                  '.png'
                ]}
                buttonKind="tertiary"
                size="small"
                buttonLabel="Add files"
                filenameStatus="edit"
                iconDescription="Clear file"
                labelDescription="only .png files at 500mb or less"
                labelTitle="Upload Tax ID"
                onChange={this.onTIDChange} required
              />
            </div>
            <div style={{marginBottom: '1rem', width: '500px'}} className="bx--file__container">
              <FileUploader
                accept={[
                  '.jpg',
                  '.png'
                ]}
                buttonKind="tertiary"
                size="small"
                buttonLabel="Add files"
                filenameStatus="edit"
                iconDescription="Clear file"
                labelDescription="only .png files at 500mb or less"
                labelTitle="Upload National ID"
                onChange={this.onNIDChange} required
              />
            </div>

            <Button
              kind="primary"
              tabIndex={0}
              type="submit"
              onClick={this.createSavingsAccount}>
              Create
            </Button>
            <br /><br />
            <Link to="/">Back</Link>
            <br /><br />
          </Form>
          
        </div>
      </div>
      )
    }
}

export default RequestSavingsAccount;


