import React from 'react';
import { Link } from 'react-router-dom';

class Message extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        userDetails: this.props.userInfo,
      };
    }

    render() {

      return (
        <div>
            <p>You do not have any Loan Account. <Link to={{ pathname: "/apply4Loan", state: { userDetails: this.state.userDetails } }}>Apply for Loan</Link></p>
        </div>
      )
    }
}

export default Message;


